body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#App{width:100%;height:100%;min-height:100vh;background:linear-gradient(#062d5e, #581b6d);position:relative}header{width:100%}#content-container{width:100%;height:100%;margin:0 auto;padding:20px}@media (min-width: 1100px){#content-container{width:70%}}

#mainheader-container{width:100%;height:90px;background-color:#062d5e;display:flex;justify-content:flex-end;align-items:center;position:relative;padding:0 20px}#app-title{position:absolute;left:20px;top:50%;transform:translateY(-50%);color:rgba(255,255,255,0.926);font-size:1.5rem;font-family:"Ubuntu",sans-serif;font-weight:800}@media (min-width: 1100px){#mainheader-container{justify-content:center}#app-title{left:100px;font-size:2rem}}@media (min-width: 1600px){#app-title{left:300px;font-size:3rem}}

#searchbar-container{width:300px;height:44px;background-color:#306dae;border-radius:22px;display:flex;overflow:hidden}#searchbar-container #search-input,#searchbar-container #search-button{height:100%;background-color:transparent;border:none;outline:none;text-align:center;color:rgba(255,255,255,0.926);font-family:"Ubuntu",sans-serif;font-size:1.5rem;font-weight:600;display:flex;justify-content:center;align-items:center}#searchbar-container #search-input:-ms-input-placeholder, #searchbar-container #search-button:-ms-input-placeholder{color:rgba(255,255,255,0.926)}#searchbar-container #search-input::placeholder,#searchbar-container #search-button::placeholder{color:rgba(255,255,255,0.926)}#searchbar-container #search-input:-ms-input-placeholder,#searchbar-container #search-button:-ms-input-placeholder{color:rgba(255,255,255,0.926)}#searchbar-container #search-input::-ms-input-placeholder,#searchbar-container #search-button::-ms-input-placeholder{color:rgba(255,255,255,0.926)}#searchbar-container #search-input{width:85%}#searchbar-container #search-input:focus{background-color:rgba(255,255,255,0.926);color:#000}#searchbar-container #search-input:focus:-ms-input-placeholder{color:#000;opacity:0.5}#searchbar-container #search-input:focus::placeholder{color:#000;opacity:0.5}#searchbar-container #search-button{width:15%;cursor:pointer;opacity:0.7}#searchbar-container #search-button:hover{opacity:1}

#subheader-container{width:100%;height:40px;background-color:#306dae;padding:0 20px;display:flex;align-items:center;grid-column-gap:10px;column-gap:10px;color:rgba(255,255,255,0.926);font-family:"Ubuntu",sans-serif;font-size:1rem;position:relative}#error-text{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);font-size:1.5rem;color:#d3d3d3;font-weight:800}#subheader-name{font-weight:800}#subheader-icon{width:40px;height:40px}@media (min-width: 1100px){#subheader-container{padding:0 100px}}@media (min-width: 1600px){#subheader-container{padding:0 300px}}

img{filter:drop-shadow(0 0 60px white)}

nav{width:100%;height:40px;background-color:#150338;display:flex;justify-content:center;grid-column-gap:50px;column-gap:50px}@media (min-width: 1100px){nav{grid-column-gap:200px;column-gap:200px}}

.nav-button-container{color:rgba(255,255,255,0.926);font-size:1.5rem;font-weight:600;background-color:transparent;height:100%;padding:0 20px;border-top:2px solid #150338;border-bottom:2px solid #150338;border-left:none;border-right:none;display:flex;justify-content:center;align-items:center;cursor:pointer}.nav-button-container:hover,.nav-button-container:focus{border-bottom:2px solid rgba(255,255,255,0.926);outline:none}.active-button{border-bottom:2px solid rgba(255,255,255,0.926)}

#currentweather-container{width:100%;max-width:1100px;height:250px;margin:0 auto;padding:35px;border-radius:7px;overflow:hidden;background-color:rgba(255,255,255,0.104);display:flex;align-items:center;justify-content:space-between;color:rgba(255,255,255,0.926);font-family:"Ubuntu",sans-serif}#current-left-content,#current-right-content{height:100%;display:flex;flex-direction:column}#current-left-content{align-items:flex-start}#current-right-content{align-items:flex-end;justify-content:center}#current-location{margin:0;font-size:1.5rem;font-weight:600;opacity:0.8}#big-temp{font-size:6rem;font-weight:800;margin:0}#current-description{margin:0;font-size:2rem;font-weight:600;opacity:0.8}#huge-icon{margin:0}#min-max-temp{margin:0;font-size:2rem;font-weight:600;opacity:0.8;text-align:center;position:relative;top:-10px}

#todayforecast-container{width:100%;max-width:1100px;height:320px;margin:20px auto 0 auto;padding:35px;border-radius:7px;background-color:#f0efef;position:relative;display:flex;align-items:center;color:#000}#todayforecast-title{color:#000;font-size:1.5rem;font-weight:800;position:absolute;top:20px;left:20px}#quarterday-container{width:100%;height:150px;display:flex;justify-content:center;align-items:center}.divider{width:1px;height:100%;background:linear-gradient(#f0efef, grey, #f0efef)}

.quarterdayforecast-container{width:24%;height:100%;background-color:#f0efef;display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative;color:#000;font-weight:800;font-family:"Ubuntu",sans-serif}.quarterday-title{font-size:1rem;opacity:0.5;margin:0}.quarterday-temp{font-size:3rem;opacity:0.6;margin:0;position:relative;top:10px}.quarterday-precipitation{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.quarterday-precipitation p{font-size:1.5rem;opacity:0.6}@media (min-width: 1100px){.quarterday-title{font-size:1.5rem}}

#todaysweather-container{width:100%;height:100%;max-width:1100px;margin:20px auto 0 auto;padding:20px;border-radius:7px;background-color:#f0efef;display:flex;flex-direction:column;justify-content:space-between;font-family:"Ubuntu",sans-serif}#todaysweather-title{width:100%;font-size:1.5rem;font-weight:800;color:#000}.split-left-right{width:100%;display:flex;justify-content:space-between;padding:0}.flex-split{width:100%;display:flex;flex-direction:column;margin-top:30px}#todays-temp{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}#actual-temp{font-size:6rem;line-height:6rem;font-weight:600;opacity:0.9;margin:0}#feels-like-temp{font-size:1.5rem;font-weight:800;opacity:0.6;margin:0;line-height:1.5rem}.todays-details{width:100%;padding:0 10px}@media (min-width: 1100px){.flex-split{flex-direction:row}.todays-details{width:49%}}@media (min-width: 500px){.split-left-right{padding:0 20px}}

#sunriseset-container{display:flex;flex-direction:column;justify-content:center;align-items:center}#sun-outline{width:100px;height:50px;border-top-left-radius:50px;border-top-right-radius:50px;border-top:4px solid #ff6f00;border-left:4px solid #ff6f00;border-right:4px solid #ff6f00}#rise-set-data{height:30px;display:flex;justify-content:center;align-items:flex-end;font-size:1.5rem}#rise-set-data div{color:orange;padding:0 6px}#rise-set-data p{line-height:1.5rem;font-family:"Ubuntu",sans-serif;font-weight:600;opacity:0.6;margin:0}

.todaysdetails-container{width:100%;height:45px;border-top:1px solid rgba(0,0,0,0.125);display:flex;justify-content:space-between;font-size:1.5rem;font-weight:600;font-family:"Ubuntu",sans-serif;color:#000}.icon-description{display:flex;justify-content:flex-start;align-items:center;opacity:0.7}.todaysdetails-data{display:flex;justify-content:flex-end;align-items:center;opacity:0.7;padding:0 20px}.details-mat-icon{width:64px;text-align:center;opacity:0.8}

#hourlydata-container{width:100%;max-width:1100px;height:100%;margin:0 auto;border-radius:7px;font-family:"Ubuntu",sans-serif}#hours-container{padding:0 20px 20px 20px;background-color:#f0efef;border-bottom-left-radius:7px;border-bottom-right-radius:7px}#hourly-header{padding:35px;background:linear-gradient(rgba(255,255,255,0.926), #d3d3d3);border-top-left-radius:7px;border-top-right-radius:7px}#hourly-title{font-size:1.5rem;font-weight:600;opacity:0.9}#hourly-title span{font-size:2rem;font-weight:800}#hourly-date{font-size:1.5rem;font-weight:800;opacity:0.8}

.onehour-container{width:100%;height:60px;background-color:#f0efef;border-top:1px solid #d3d3d3;padding:0 20px;display:flex;justify-content:space-between;align-items:center;font-family:"Ubuntu",sans-serif;cursor:pointer}.onehour-container:hover{opacity:0.6}.onehour-extended-container{width:100%;height:100%;padding:0 20px 20px 20px;background:linear-gradient(#f0efef, #e8e6e6);cursor:pointer}.onehour-leftside,.onehour-rightside{display:flex;align-items:center;font-family:"Ubuntu",sans-serif;color:#000;grid-column-gap:20px;column-gap:20px}.onehour-rightside{justify-content:space-between}.onehour-time{font-size:1.2rem;width:75px;text-align:center}.onehour-extended-title{font-family:"Ubuntu",sans-serif;font-size:1.5rem;font-weight:800;color:#000;padding-bottom:20px}.extended-container{width:100%;height:100%;padding:0 20px 20px 20px;border:1px solid #d3d3d3;border-radius:7px;display:flex;flex-wrap:wrap}.onehour-temp{font-size:1.5rem;font-weight:800;width:50px}.onehour-more{font-size:3rem;color:#306dae}.onehour-rain-wind,.onehour-rain,.onehour-weather{display:flex;align-items:center}.onehour-weather div{font-size:1.2rem;margin-left:3px;width:0;visibility:hidden}.onehour-rain img{width:30px;height:30px}.onehour-rain div{font-size:1.2rem;font-weight:400;margin-left:3px;width:40px;opacity:0.8}.onehour-wind{visibility:hidden;width:0;display:flex;align-items:center;opacity:0.8}.onehour-wind-icon{color:#306dae}.onehour-wind-data{font-size:1.2rem;margin-left:3px}.extra-divider{width:100%;height:1px;background-color:#d3d3d3;margin-top:20px}@media (min-width: 800px){.onehour-wind{visibility:visible;width:130px}.onehour-rain{width:70px}.onehour-weather div{visibility:visible;width:100%}.onehour-extended-title{visibility:hidden;height:0;padding:0}.extra-divider{visibility:hidden;height:0;width:0}}

.extendeddetail-container{width:50%;padding-top:20px;display:flex}.extra-icon{color:#306dae;margin-right:20px}.extra-data{font-size:1.2rem;font-weight:800;font-family:"Ubuntu",sans-serif}@media (min-width: 800px){.extendeddetail-get-smaller{width:25%}}

#weekweather-container{width:100%;max-width:1100px;height:100%;margin:0 auto;border-radius:7px;font-family:"Ubuntu",sans-serif}#weekweather-header{width:100%;height:100%;padding:35px;border-top-left-radius:7px;border-top-right-radius:7px;background:linear-gradient(rgba(255,255,255,0.926), #d3d3d3)}#weekweather-header p{font-size:1.5rem;font-weight:800;padding-bottom:35px}#weekweather-header p span{font-size:2rem}#weekweather-days{padding:0 20px 20px 20px;background-color:#f0efef;border-bottom-left-radius:7px;border-bottom-right-radius:7px}

.daysummary-main-container{width:100%;height:60px;background-color:#f0efef;border-top:1px solid #d3d3d3;padding:0 20px;display:flex;justify-content:space-between;align-items:center;font-family:"Ubuntu",sans-serif;cursor:pointer}.daysummary-main-container:hover{opacity:0.6}.daysummary-extra-full-container{width:100%;height:100%;background:linear-gradient(#f0efef, #e8e6e6);border-top:1px solid #d3d3d3;font-family:"Ubuntu",sans-serif;cursor:pointer;position:relative}.daysummary-extra-full-container:hover{opacity:0.6}.extra-wrapper{padding:0 20px 20px 20px;display:flex;flex-direction:column}.daysummary-leftside,.daysummary-rightside{display:flex;align-items:center;font-family:"Ubuntu",sans-serif;color:#000;grid-column-gap:20px;column-gap:20px}.daysummary-more{font-size:3rem;color:#306dae}.daysummary-less{font-size:3rem;color:#306dae;width:100%;height:60px;display:flex;justify-content:flex-end;align-items:center;position:absolute;right:20px}.daysummary-rightside{justify-content:space-between}.daysummary-date{font-size:1.2rem;width:65px;text-align:left}.daysummary-temp{font-size:1.5rem;font-weight:800;width:70px}.daysummary-rain-wind,.daysummary-rain,.daysummary-weather{display:flex;align-items:center}.daysummary-weather div{font-size:1.2rem;margin-left:3px;width:0;visibility:hidden}.daysummary-rain img{width:30px;height:30px}.daysummary-rain div{font-size:1.2rem;font-weight:400;margin-left:3px;width:40px;opacity:0.8}.daysummary-wind{visibility:hidden;width:0;display:flex;align-items:center;opacity:0.8}.daysummary-wind-icon{color:#306dae}.daysummary-wind-data{font-size:1.2rem;margin-left:3px}@media (min-width: 800px){.daysummary-wind{visibility:visible;width:130px}.daysummary-rain{width:70px}.daysummary-weather div{visibility:visible;width:100%}.extra-wrapper{padding:0;flex-direction:row;justify-content:space-evenly}}

.dayextra-container{width:100%;height:100%;display:flex;flex-direction:column;padding-top:20px}.day-extended-container{width:100%;height:100%;display:flex;flex-direction:column;border:1px solid #d3d3d3;border-radius:7px;padding:0 20px 20px 20px;margin-top:20px}.day-extended-wrapper{display:flex}.always-divider{width:100%;height:1px;background-color:#d3d3d3;margin-top:20px}.dayextra-header{font-size:1.5rem}.dayextra-header span{font-weight:800}.extra-main-content{width:100%;height:100%;display:flex;justify-content:space-between;align-items:center;margin-top:10px}.extra-big-temp{font-size:4.5rem;font-weight:800;padding:20px 0}.extra-wind-rain{width:50%;height:100%;display:flex;flex-direction:column}.wind-or-rain{display:flex;align-items:center;padding:3px 0}.wind-or-rain img{width:24px;height:24px}.wind-or-rain .icon-text{font-size:1.5rem;font-weight:400;padding-left:5px}.blue-icon{color:#306dae}.moon-phase{width:100%;height:30px;display:flex;align-items:center;padding-top:10px}.moon-phase p{margin-left:5px;font-weight:800;font-size:1.2rem}.moon-phase div{opacity:0.7}.no-moon-phase{width:100%;height:0}@media (min-width: 800px){.dayextra-container{width:47%;margin-bottom:20px}.no-moon-phase{height:30px}}

#loading-container{width:300px;height:300px;background-color:rgba(0,0,0,0.815);border-radius:40px;position:absolute;top:calc(100vh / 2);left:calc(100vw / 2);transform:translate(-50%, -50%);z-index:2;display:flex;justify-content:center;align-items:center}.spinner{width:200px;height:200px;border-top:10px solid #306dae;border-radius:100px;animation:spin 5s linear infinite}.spinner-white{border-color:#f0efef}.spinner-dark{border-color:#062d5e}@keyframes spin{100%{transform:rotate(720deg)}}

